import { storage } from 'firebase';
import React, { useState } from 'react';
import { useSelector } from "react-redux";
import { useFirebase, useFirestore } from "react-redux-firebase";
import { useHistory } from "react-router-dom";
import '../../sass/newBlogCreator.scss';
import 'firebase/storage';

const NewBlogCreation = () => {
    const { displayName, uid } = useSelector((state) => state.firebase.auth);
    const firestore = useFirestore();

    const [imgValue, setImg] = useState(null);
    const [blogTitle, setTitle] = useState('');
    const [smallDescription, setSmallDescription] = useState();
    const history = useHistory();
    const firebase = useFirebase();
    const [description, setDescription] = useState("");

    const handleSubmit = () => {
        const storageRef = firebase.storage().ref();
        let uploadTask = storageRef.child(`images/${imgValue.name}`).put(imgValue);

        uploadTask.on('state_changed', function(snapshot){
            switch (snapshot.state) {
              case firebase.storage.TaskState.PAUSED: // or 'paused'
                console.log('Upload is paused');
                break;
              case firebase.storage.TaskState.RUNNING: // or 'running'
                console.log('Upload is running');
                break;
            }
          }, function(error) {
                console.log('error: ', error);  
          }, function() {
            uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
                if(downloadURL){
                    setTimeout(() => {
                        firestore
                            .collection("users")
                            .doc(uid)
                            .collection("blogs")
                            .add({
                                title: blogTitle,
                                smallDescription: smallDescription,
                                image: downloadURL, 
                                description: description,
                            })
                            .then((docRef) => {
                                docRef.update({
                                    blogID: docRef.id,
                                });
                            })
                    },2000)
                    history.push('/profile');
                }
            });
          });
    }

    const handleImgChange = (event) => {
        if (event.target.files[0]) {
            setImg(event.target.files[0])
        }
    }

    const handleSmlDescChange = (e) => {
        setSmallDescription(e.target.value);
    }

    const handleTitleChange = (e) => {
        setTitle(e.target.value);
    }

    const handleDesChange = (e) => {
        setDescription(e.target.value);
    }

    const signout = () => {
        firebase
            .auth()
            .signOut()
            .then(() => {
                history.push(process.env.PUBLIC_URL + "/");
            });
    }

    return (
        <div>
            <nav className="navbar navbar-expand-sm bg-dark navbar-dark navbarContent">
                <a href="#" className="navbar-brand"><strong>B</strong>log</a>
                <h2 className='text-light d-inline p-0 ml-auto mt-auto mb-auto'>Create Your Blog</h2>
                <div className='dropdown profileDropdown'>
                    <button className='btn btn-info rounded-circle roundProfileBtn' data-toggle="dropdown">{displayName[0]}</button>
                    <div className="dropdown-menu dropdown-menu-right">
                        <a className="dropdown-item font-weight-bold" href="#">{displayName}</a>
                        <a className="dropdown-item" href="#" onClick={(event) => { event.preventDefault(); signout(); }}>SignOut</a>
                    </div>
                </div>
            </nav>
            <div className="container formContainer">
                <form action="" onSubmit={(e) => { e.preventDefault(); handleSubmit(); }}>
                    <table className="table table-borderless">
                        <tbody>
                            <tr>
                                <td>Title</td>
                                <td>
                                    <div className="form-group">
                                        <input type="text" className="form-control" id="title" placeholder="Enter title" name="title" value={blogTitle} onChange={handleTitleChange} />
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>SmallDescription</td>
                                <td>
                                    <div className="form-group">
                                        <textarea className="form-control" rows="2" id="smallDesc" placeholder="Enter small description" name="smallDesc" value={smallDescription} onChange={handleSmlDescChange}/>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>Image</td>
                                <td>
                                    <div className="form-group">
                                        <input type="file" className="form-control-file border" id="img" name="img" onChange={handleImgChange}/>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <td>Description</td>
                                <td>
                                    <div className="form-group">
                                        <textarea className="form-control" rows="5" name="desc" value={description} onChange={handleDesChange}/>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <input type="submit" className="btn btn-success" value="PUBLISH" />
                </form>
            </div>
        </div>
    );
}

export default NewBlogCreation;